module.exports = {
	// 节点名称
	name: 'icon',
	draw: function draw(item) {
		var group = item.getGraphicGroup();
		var model = item.getModel();
		return group.addShape('image', {
			attrs: {
				x: 10,
				y: this.config.height / 2 - 12,
				img: model.nodeIcon || 'public/images/' + this.config.defaultIcon,
				width: 24,
				height: 24
			}
		});
	}
};