module.exports = {
    //节点名称
    name: 'info',
    draw: function draw(item) {
        var group = item.getGraphicGroup();
        var model = item.getModel();
        if (model.infoAble === true) {
            return group.addShape('image', {
                attrs: {
                    x: this.config.width - 24 - 6,
                    y: -9,
                    img: this.config.infoIcon,
                    width: 16,
                    height: 16
                }
            });
        }
        return group;
    }
};