module.exports = {
	//节点名称
	name: 'delete',
	draw: function draw(item) {
		var group = item.getGraphicGroup();
		var model = item.getModel();
		if (model.delAble === true && model.activity) {
			return group.addShape('image', {
				attrs: {
					x: this.config.width - 10,
					y: -10,
					img: this.config.deleteIcon,
					width: 20,
					height: 20
				}
			});
		}
		return group;
	}
};