module.exports = {
    //节点名称
    name: 'ghostStep',
    draw: function draw(item) {
        var group = item.getGraphicGroup();
        var model = item.getModel();
        return group.addShape('rect', {
            attrs: {
                x: 0,
                y: 0,
                width: this.config.width,
                height: this.config.height,
                radius: this.config.radius,
                lineWidth: 1,
                stroke: '#01befa',
                fill: 'rgba(1, 180, 250, .5)'
            }
        });
    }
};