var _require = require('util/common.js'),
    getCubicController = _require.getCubicController;

module.exports = {
  //节点名称
  name: 'group',
  //draw function
  draw: function draw(item) {
    var group = item.getGraphicGroup();
    var model = item.getModel();
    var source = model.source,
        target = model.target,
        activity = model.activity,
        c1 = model.c1,
        c2 = model.c2;

    var x = void 0,
        y = void 0,
        width = void 0,
        height = void 0;
    var p1 = void 0,
        p2 = void 0;

    var tempObj = getCubicController(source, target);
    c1 = c1 || tempObj.c1;
    c2 = c2 || tempObj.c2;

    x = Math.min(target.x, source.x);
    y = Math.min(target.y, source.y);
    width = Math.abs(target.x - source.x);
    height = Math.abs(target.y - source.y);

    // 外线框
    var keyShape = group.addShape('rect', {
      attrs: {
        x: x,
        y: y,
        width: width,
        height: height,
        lineWidth: 1
      }
    });

    p1 = [source.x, source.y];

    p2 = [target.x, target.y];

    var attrs = {
      stroke: '#eb7a0b',
      opacity: activity ? 1 : 0
      //clip: keyShape
    };

    attrs.x1 = p1[0];
    attrs.y1 = p1[1];
    attrs.x2 = c1[0];
    attrs.y2 = c1[1];
    group.addShape('line', {
      attrs: attrs
    });

    attrs.x1 = p2[0];
    attrs.y1 = p2[1];
    attrs.x2 = c2[0];
    attrs.y2 = c2[1];
    group.addShape('line', {
      attrs: attrs
    });

    return group;
  },
  _beforeDraw: function _beforeDraw() {
    console.log(233);
  },
  afterDraw: function afterDraw() {},

  anchor: function anchor(item) {
    var model = item.getModel();
    return model.anchor || [];
  }
};