module.exports = {
		//节点名称
		name: 'group',
		//draw function
		draw: function draw(item) {
				var group = item.getGraphicGroup();
				var model = item.getModel();
				if (undefined === model.edges) model.edges = [];
				// 外线框
				var keyShape = group.addShape('rect', {
						attrs: {
								x: model.x,
								y: model.y,
								width: model.width,
								height: model.height,
								radius: 6,
								lineWidth: 1
								//  stroke: model.isActive ? '#666' : '#666'
						}
				});
				// 上背景
				group.addShape('rect', {
						attrs: {
								x: model.x,
								y: model.y,
								width: model.width,
								height: model.height,
								fill: 'red',
								clip: keyShape
						}
				});
				/*
      group.addShape('text', {
         	attrs: {
           	x: model.x + 50,
            y: model.y + 25,
            fill: '#333',
            text : '测试组名称',
            textBaseline : 'middle',
            textAlign : 'start',
            fontSize:'20',
         }
       });*/
				return group;
		},
		_beforeDraw: function _beforeDraw() {
				console.log(233);
		},
		afterDraw: function afterDraw() {},

		anchor: function anchor(item) {
				var model = item.getModel();
				return model.anchor || [];
		}
};